/* Base */
@font-face {
  font-family: "Alexandra Zeferino One";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(../fonts/AlexandraZeferinoOne.woff2) format("woff2");
}
:root {
  --container-width: 1162px;
  --container-padding: 30px;
  --index: calc(100vw + 100vh);
  --font-main: "Roboto", sans-serif;
  --font-title: "Alexandra Zeferino One", sans-serif;
  --font-size-base: 16px;
  --font-color-title: #656565;
  --bg-button: #e9b384;
  --bg-button-hover: #c88139;
  --bg-light: #fff;
  --bg-dark: #504f4f;
  --border: 2px solid #e9b384;
  --shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  --bg-round-width:calc(var(--index) / 2.17);
  --bg-round-height:calc(var(--index) / 2.83);
}

/*
.dark {
    --page-bg: #252526;
    --text-color: #fff;
}*/
@media (max-width: 768px) {
  :root {
    --container-padding: 10px;
  }
}
html {
  scroll-behavior: smooth;
  overflow-y: auto;
}

body {
  background-color: var(--page-bg);
  color: var(--text-color);
  font-family: var(--font-main);
  font-size: var(--font-size-base);
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
  max-width: 100%;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--font-color-title);
}

h2 {
  font-family: var(--font-title);
  font-size: 100px;
  font-weight: 400;
  line-height: 100%;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  h2 {
    font-size: 70px;
    margin-bottom: 30px;
  }
}
.none {
  display: none !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-scroll {
  overflow-y: hidden;
}

.container {
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  margin: 0 auto;
}

.container-right {
  /* overflow-x: hidden; */
  padding-left: calc((100% - var(--container-width)) / 2 + var(--container-padding));
}
@media (max-width: var(--laptop-size)) {
  .container-right {
    padding-left: var(--container-padding);
  }
}

.container-left {
  /* overflow-x: hidden; */
  padding-right: calc((100% - var(--container-width)) / 2 + var(--container-padding));
}
@media (max-width: var(--laptop-size)) {
  .container-left {
    padding-right: var(--container-padding);
  }
}

html, body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.footer {
  margin-top: auto;
}

.footer {
  padding: 60px 0;
  background-color: #e3e3e3;
}

/* Blocks */
.about-author {
  padding-bottom: 90px;
  padding-top: 50px;
}
.about-author__grid {
  display: flex;
  gap: 30px;
}
.about-author__text {
  text-align: left;
  max-width: 570px;
}
.about-author__text p {
  margin-bottom: 15px;
}
.about-author__text b {
  font-weight: 400;
}
.about-author__wrapper-btn {
  margin-top: 33px;
}
.about-author__image {
  max-width: 282px;
  margin: 0 50px 0 auto;
}
.about-author__image img {
  border-radius: 30px;
}

@media (max-width: 992px) {
  .about-author__image {
    flex-shrink: 0.2;
    margin: 0;
  }
}
@media (max-width: 768px) {
  .about-author {
    padding-bottom: 15px;
  }
  .about-author__grid {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.button {
  width: fit-content;
  border-radius: 30px;
  background-color: var(--bg-button);
  color: var(--bg-light);
  font-weight: 400;
  padding: 18px 40px;
  transition: ease-in 0.2s;
}
.button--link {
  border-radius: 30px;
  outline: var(--border);
  background-color: var(--bg-light);
  color: var(--font-color-title);
  padding: 14px 36px;
}
.button:hover {
  background-color: var(--bg-button-hover);
}

a.button {
  display: inline-block;
}
a.button:hover {
  color: var(--bg-light);
  outline: none;
}

@media (max-width: 768px) {
  .button {
    padding: 15px 30px;
  }
  .button--link {
    padding: 11px 26px;
  }
}
.card-material {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card {
  border-radius: 30px;
  border: var(--border);
  background: var(--bg-light);
  padding: 56px 0;
  max-width: 360px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.card__title {
  font-size: 18px;
  font-weight: 500;
  line-height: 150%; /* 27px */
}

.cards__container {
  padding: 200px 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 40px;
}

@media (max-width: 768px) {
  .cards__container {
    justify-items: center;
    padding: 50px 0;
    gap: 20px;
  }
}
.footer {
  background-color: var(--bg-dark);
  padding: 88px 0 119px 0;
  color: var(--bg-light);
}
.footer * a {
  color: var(--bg-light);
  transition: ease-in 0.2s;
}
.footer * a:hover {
  color: var(--bg-button);
}
.footer * a:hover svg:hover {
  fill: var(--bg-button);
}
.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 40px;
}
.footer__left {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.footer__copy {
  opacity: 0.6;
}
.footer__nav * a {
  text-align: right;
  text-decoration-line: underline;
}

@media (max-width: 768px) {
  .footer__container {
    align-items: center;
    flex-wrap: wrap;
  }
  .footer__copy a {
    color: var(--bg-button);
  }
}
.free-materials {
  position: relative;
  text-align: center;
  margin-bottom: 90px;
}
.free-materials::before, .free-materials::after {
  content: "";
  position: absolute;
  width: var(--bg-round-width);
  height: var(--bg-round-height);
  max-width: 1087px;
  max-height: 831px;
  border-radius: 50%;
  background-color: #b7d2e2;
  z-index: -10;
}
.free-materials::before {
  right: 50%;
  top: -5%;
}
.free-materials::after {
  left: 50%;
  bottom: -10%;
}
.free-materials__container {
  border-radius: 30px;
  border: var(--border);
  background-color: var(--bg-light);
  padding: 75px 60px 0 60px;
}
.free-materials__title {
  margin-bottom: 23px;
}
.free-materials__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  justify-items: center;
  justify-content: center;
  margin-bottom: 100px;
}
.free-materials__image {
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .free-materials__container {
    padding: 40px 20px;
  }
  .free-materials__grid {
    margin-bottom: 50px;
  }
}
.header {
  padding: 86px 0 0 0;
  position: relative;
}
.header::after, .header::before {
  content: "";
  position: absolute;
  width: var(--bg-round-width);
  height: var(--bg-round-height);
  max-width: 1087px;
  max-height: 831px;
  border-radius: 50%;
}
.header::before {
  top: -4%;
  right: 40%;
  background-color: #fdf8f8;
  z-index: -9;
}
.header::after {
  top: -4%;
  left: 50%;
  background-color: #b7d2e2;
  z-index: -10;
}
.header__grid {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  column-gap: 60px;
  align-items: center;
  position: relative;
  margin-bottom: 100px;
  padding-top: 50px;
}
.header__image {
  position: relative;
  max-width: 350px;
  margin-left: auto;
}
.header__image::before {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 30px;
  border: 2px solid #ede8de;
  bottom: -30px;
  left: -30px;
  z-index: -1;
  position: absolute;
}
.header__image img {
  border-radius: 30px;
  box-shadow: var(--shadow);
}
.header__name {
  color: var(--font-color-title);
  font-family: var(--font-title);
  font-size: 100px;
  font-weight: 400;
  line-height: 100%;
}
.header__title {
  margin-bottom: 30px;
  font-size: 12.5px;
}
.header-smoll {
  display: block;
  font-weight: 400;
  margin-bottom: 14px;
}
.header__row {
  display: flex;
  gap: 30px;
  margin-bottom: 100px;
  border-radius: 30px;
  border: var(--border);
  background-color: #fff;
  padding: 70px 36px 60px 60px;
}
.header__text {
  max-width: 662px;
  margin-top: 25px;
  flex-shrink: 1.5;
}
.header__text p {
  margin-bottom: 14px;
  padding-left: 25px;
}
.header__text p:last-of-type {
  margin-bottom: 100px;
}
.header__text > :first-child {
  background-image: url(../img/quotes.svg);
  background-repeat: no-repeat;
  background-position: left 0 top 0;
}
.header__text span {
  color: var(--font-color-title);
  font-family: var(--font-title);
  font-size: 30px;
  font-weight: 400;
  line-height: 100%;
  display: block;
  text-align: end;
}
.header__button {
  margin: 0 20px 20px 0;
}
.header__author-img img {
  border-radius: 30px;
}

@media (max-width: 992px) {
  .header__grid {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  .header__descr {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .header::before {
    top: 0;
    right: 0;
    width: calc(var(--bg-round-height) * 2);
    height: calc(var(--bg-round-height) * 2);
  }
  .header__grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  .header__image {
    margin: 0 auto;
    max-width: 270px;
  }
  .header__image::before {
    bottom: -15px;
    left: -15px;
  }
  .header__title--smoll {
    text-align: left;
  }
  .header__row {
    flex-wrap: wrap;
    padding: 40px 20px;
  }
  .header__author-img {
    max-width: 350px;
    margin: 0 auto;
  }
  .header__text p:last-of-type {
    margin-bottom: 50px;
  }
}
.logo {
  font-size: 32px;
}

.mobile-nav {
  position: fixed;
  top: -100%;
  width: 100%;
  height: 100%;
  z-index: 99;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background: rgb(130, 156, 168);
  transition: all 0.2s ease-in;
}

.mobile-nav--open {
  top: 0;
}

.mobile-nav a {
  color: #fff;
}

.mobile-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  font-size: 28px;
}
.mobile-nav__list .active {
  opacity: 0.5;
}

.more-details-planer {
  position: relative;
}
.more-details-planer::before, .more-details-planer::after {
  content: "";
  position: absolute;
  width: var(--bg-round-width);
  height: var(--bg-round-height);
  max-width: 1087px;
  max-height: 831px;
  border-radius: 50%;
  background-color: #b7d2e2;
  z-index: -10;
}
.more-details-planer::before {
  right: 50%;
  top: -5%;
}
.more-details-planer::after {
  left: 50%;
  bottom: -10%;
}
.more-details-planer__container {
  padding: 76px 54px 46px 60px;
  border-radius: 30px;
  border: var(--border);
  background: var(--bg-light);
  position: relative;
}
.more-details-planer__container::after {
  content: "";
  position: absolute;
  left: -115px;
  top: calc(50% - 25px);
  transform: translateY(-50%) rotate(32.42deg);
  width: 180px;
  height: 228px;
  background-image: url(../img/planer.png);
}
.more-details-planer__row {
  display: flex;
  gap: 45px;
  justify-content: space-between;
  margin-bottom: 40px;
}
.more-details-planer__title {
  text-align: center;
}
.more-details-planer__list-text {
  max-width: 600px;
}
.more-details-planer__list-text b {
  color: var(--text-color);
  font-weight: 500;
  margin-bottom: 18px;
  display: block;
}
.more-details-planer__list-text b:last-of-type {
  margin-bottom: 33px;
  display: block;
}
.more-details-planer__list-text li {
  padding-left: 30px;
  margin-bottom: 5px;
  position: relative;
}
.more-details-planer__list-text li::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background-color: var(--bg-button);
  top: 9px;
  left: 0;
}
.more-details-planer__list-text--check li::after {
  content: url(../img/check.svg);
  background-color: transparent;
  top: 0;
}
.more-details-planer__list-text--check ul {
  margin-bottom: 24px;
}
.more-details-planer__footnote {
  max-width: 386px;
  font-size: 14px;
}
.more-details-planer__image {
  flex-shrink: 0.2;
  max-width: 270px;
}
.more-details-planer__image img {
  border-radius: 30px;
}

@media (max-width: 768px) {
  .more-details-planer__container {
    padding: 40px 20px;
  }
  .more-details-planer__container::after {
    content: none;
  }
  .more-details-planer__row {
    margin-bottom: 40px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
/* Nav Icon */
.mobile-nav-btn {
  --time: 0.1s;
  --width: 40px;
  --height: 40px;
  --line-height: 3px;
  --spacing: 8px;
  --color: var(--bg-button);
  --radius: 4px;
  /* Fixed height and width */
  /* height: var(--height); */
  /* width: var(--width); */
  /* Dynamic height and width */
  height: calc(var(--line-height) * 3 + var(--spacing) * 2);
  width: var(--width);
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-icon {
  position: relative;
  display: inline-block;
  width: var(--width);
  height: var(--line-height);
  background-color: var(--color);
  border-radius: var(--radius);
}

.nav-icon::before,
.nav-icon::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: var(--width);
  height: var(--line-height);
  border-radius: var(--radius);
  background-color: var(--color);
  transition: transform var(--time) ease-in, top var(--time) linear var(--time);
}

.nav-icon::before {
  /* top: calc(var(--line-height) * -2); */
  top: calc(-1 * (var(--line-height) + var(--spacing)));
}

.nav-icon::after {
  /* top: calc(var(--line-height) * 2); */
  top: calc(var(--line-height) + var(--spacing));
}

.nav-icon.nav-icon--active {
  background-color: transparent;
}

.nav-icon.nav-icon--active::before,
.nav-icon.nav-icon--active::after {
  top: 0;
  transition: top var(--time) linear, transform var(--time) ease-in var(--time);
}

.nav-icon.nav-icon--active::before {
  transform: rotate(45deg);
}

.nav-icon.nav-icon--active::after {
  transform: rotate(-45deg);
}

/* Layout */
.mobile-nav-btn {
  z-index: 999;
  display: none;
  position: absolute;
  right: 25px;
  top: 25px;
}

@media (max-width: 768px) {
  .mobile-nav-btn {
    display: block;
  }
}
.nav {
  position: relative;
}
.nav__list {
  display: flex;
  gap: 25px;
  align-items: center;
}
.nav__link {
  color: var(--font-color-title);
  font-size: 14px;
  font-weight: 500;
  transition: ease-in 0.2s;
}
.nav__link:hover {
  color: var(--bg-button-hover);
}

@media (max-width: 768px) {
  .mobile-nav---open {
    transition: ease-in 0.3s;
  }
  .nav {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: -100%;
    background-color: transparent;
    transition: ease-in 0.3s;
  }
  .nav__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 100vh;
    padding: 40px;
    width: 100vw;
    align-items: flex-start;
    justify-content: center;
    position: absolute;
    visibility: hidden;
    top: -100vh;
    left: 50%;
    transform: translateX(-50%);
    transition: ease-in 0.3s;
  }
  .nav.mobile-nav--open {
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: rgba(34, 60, 80, 0.75);
    z-index: 100;
    top: 0;
    transition: ease-in 0.3s;
    overflow-y: auto;
  }
  .nav.mobile-nav--open .nav__list {
    visibility: visible;
    top: 0;
    transition: ease-in 0.3s;
  }
  .nav.mobile-nav--open .nav__list a {
    color: var(--bg-light);
    font-size: 20px;
  }
}