html {
  scroll-behavior: smooth;
  overflow-y: auto;
}

body {
  background-color: var(--page-bg);
  color: var(--text-color);
  font-family: var(--font-main);
  font-size: var(--font-size-base);
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
  max-width: 100%;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--font-color-title);
}
h2 {
  font-family: var(--font-title);
  font-size: 100px;
  font-weight: 400;
  line-height: 100%;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  h2 {
    font-size: 70px;
    margin-bottom: 30px;
  }
}
